import "./index.css";
import paper from "./images/icon-paper.svg";
import scissors from "./images/icon-scissors.svg";
import rock from "./images/icon-rock.svg";
import { useState, useEffect } from "react";
import { Header } from "./components/Header";

function App() {
  const [userChoice, setUserChoice] = useState(null);
  const [computerChoice, setComputerChoice] = useState(null);
  const [result, setResult] = useState(null);
  const [score, setScore] = useState(0);

  const choices = ["rock", "paper", "scissors"];

  const handleClick = (value) => {
    setUserChoice(value);
    generateComputerChoice();
  };

  const generateComputerChoice = () => {
    const randomChoice = choices[Math.floor(Math.random() * choices.length)];
    setComputerChoice(randomChoice);
  };

  useEffect(() => {
    switch (userChoice + computerChoice) {
      case "scissorspaper":
      case "rockscissors":
      case "paperrock":
        const win = setResult("YOU WIN!");
        console.log(win);
        break;
      case "paperscissors":
      case "scissorsrock":
      case "rockpaper":
        const lose = setResult("YOU LOSE!");
        break;
      case "rockrock":
      case "paperpaper":
      case "scissorsscissors":
        setResult("ITS A DRAW!");
        break;
    }
  }, [userChoice, computerChoice]);

  return (
    <>
      <Header score={score} />
      <div className="middle">
        <div className="wrapper btn-circle btn-paper">
          <img
            src={paper}
            onClick={() => handleClick(choices[1])}
            alt="paper"
          />
        </div>
        <div className="wrapper btn-circle btn-scissors">
          <img
            src={scissors}
            onClick={() => handleClick(choices[2])}
            alt="scissors"
          />
        </div>
        <div className="wrapper btn-circle btn-rock">
          <img src={rock} onClick={() => handleClick(choices[0])} alt="rock" />
        </div>
      </div>
      <h1>User Choice is: {userChoice}</h1>
      <h1>Computer Choice is: {computerChoice}</h1>
      <h1>{result}</h1>
    </>
  );
}

export default App;
